<template>
    <div class="Company">
        <div class="page">
            <p class="page-p" v-if="!this.havaCompany"></p>
        </div>
        <div class="company-block">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

                <el-form-item label="" prop="name">
                    <el-input v-model="ruleForm.name"></el-input>
                </el-form-item>

                <el-form-item label="" prop="address">
                    <el-input v-model="ruleForm.address"></el-input>
                </el-form-item>

                <el-form-item label="" prop="capital">
                    <el-input type="number" v-model.number="ruleForm.capital" autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item label="" prop="charge">
                    <el-input v-model="ruleForm.charge"></el-input>
                </el-form-item>

                <el-form-item label="" prop="identification">
                    <el-input v-model="ruleForm.identification"></el-input>
                </el-form-item>

                <el-form-item>

                    <el-button
                            v-if="this.ruleForm.status == 99"
                            type="primary"
                            @click="submitForm('ruleForm')"
                    >
                        
                    </el-button>

                    <el-button
                            v-if="this.ruleForm.status == 0"
                            type="primary"
                            @click="examineing('ruleForm')"
                    >
                        
                    </el-button>

                    <el-button
                            v-if="this.ruleForm.status == 1"
                            type="primary"
                            @click="updataFrom('ruleForm')"
                    >
                        
                    </el-button>

                    <el-button @click="resetForm('ruleForm')"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import { getCompanyDetail, createCompany, editCompany } from '../utils/index';
    import { getToken } from '../utils/function';
    export default {
        name: "company",
        data() {
            return {
                ruleForm: {
                    id: '',
                    name: '',
                    address: '',
                    capital: '',
                    charge: '',
                    identification: '',
                    status: 99
                },
                rules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
                    ],
                    address: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 3, max: 30, message: ' 3  20 ', trigger: 'blur' }
                    ],
                    capital: [
                        { required: true, message: ''},
                        { type: 'number', message: ''}
                    ],
                    charge: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }
                    ],
                    identification: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
                    ],
                },
                havaCompany: false
            };
        },
        methods: {
            // 
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let token = getToken()
                        createCompany({
                            token: token,
                            name: this.ruleForm.name,
                            address: this.ruleForm.address,
                            capital: this.ruleForm.capital,
                            charge: this.ruleForm.charge,
                            identification: this.ruleForm.identification
                        }).then((res) => {
                            let data = res.data
                            console.log(data)
                            if(data.code == 0){
                                this.getCompanyInfo()
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                            }else{
                                this.$alert(data.msg)
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            // 
            examineing() {
                this.$alert("")
            },
            // 
            updataFrom(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let token = getToken()
                        editCompany({
                            token: token,
                            id: this.ruleForm.id,
                            name: this.ruleForm.name,
                            address: this.ruleForm.address,
                            capital: this.ruleForm.capital,
                            charge: this.ruleForm.charge,
                            identification: this.ruleForm.identification
                        }).then((res) => {
                            let data = res.data
                            console.log(data)
                            if (data.code == 0) {
                                this.getCompanyInfo()
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                            } else {
                                this.$alert(data.msg)
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            // 
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            // 
            getCompanyInfo() {
                let token = getToken();

                // token
                getCompanyDetail({
                    token: token
                }).then((res) => {
                    let data = res.data
                    console.log(data)

                    if(data.data == null) {
                        this.havaCompany = false
                    }else {
                        this.havaCompany = true;
                        this.ruleForm.id = data.data.id
                        this.ruleForm.name = data.data.name
                        this.ruleForm.address = data.data.address
                        this.ruleForm.capital = data.data.capital
                        this.ruleForm.charge = data.data.charge
                        this.ruleForm.identification = data.data.identification
                        this.ruleForm.status = data.data.status
                    }
                })
            }
        },
        mounted() {
            // 
            this.getCompanyInfo()
        }
    }
</script>

<style lang="stylus" scoped>
    .page
        margin 30px 0
        text-align center
        .page-botton
            padding-top 20px
</style>