<template>
    <div class="navMenu">
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
            <el-menu-item index="1"></el-menu-item>
            <el-submenu index="2" v-if="this.role != 3">
                <template slot="title"></template>
                <el-menu-item index="2-1" v-if="this.role == 2"></el-menu-item>
                <el-menu-item index="2-2" v-if="this.role == 2"></el-menu-item>
                <el-menu-item index="2-3" v-if="this.role == 1"></el-menu-item>
            </el-submenu>
            <el-submenu index="3" v-if="this.role != 3">
                <template slot="title"></template>
                <el-menu-item index="3-1" v-if="this.role == 1"></el-menu-item>
                <el-menu-item index="3-2" v-if="this.role == 2"></el-menu-item>
            </el-submenu>
            <el-submenu index="4" v-if="this.role == 2">
                <template slot="title"></template>
                <el-menu-item index="4-1"></el-menu-item>
            </el-submenu>
            <el-submenu index="5" v-if="this.role == 3">
                <template slot="title"></template>
                <el-menu-item index="5-1"></el-menu-item>
                <el-menu-item index="5-2"></el-menu-item>
            </el-submenu>
        </el-menu>
    </div>
</template>

<script>
    import { getUserInfo } from '../utils/function'
    export default {
        name: "nav-menu",
        data() {
            return {
                activeIndex: '1',
                role: 1
            };
        },
        methods: {
            handleSelect(key, keyPath) {

                if(key == '1'){
                    // 
                    this.$router.push({
                        path: '/'
                    })
                }else if(key == '2-1'){
                    // 
                    this.$router.push({
                        path: '/postlist'
                    })
                }else if(key == '2-2'){
                    // 
                    this.$router.push({
                        path: '/postaddeditor'
                    })
                }else if(key == '2-3'){
                    // 
                    this.$router.push({
                        path: '/mesendpost'
                    })
                }else if(key == '3-1'){
                    // 
                    this.$router.push({
                        path: '/resumeedit'
                    })
                }else if(key == '3-2'){
                    // 
                    this.$router.push({
                        path: '/getresume'
                    })
                }else if(key == '4-1'){
                    // 
                    this.$router.push({
                        path: '/company'
                    })
                }else if(key == '5-1'){
                    // 
                    this.$router.push({
                        path: '/companyaudit'
                    })
                }else if(key == '5-2'){
                    // 
                    this.$router.push({
                        path: '/category'
                    })
                }
            }
        },
        mounted() {
            let userInfo = getUserInfo()
            console.log(`userInfo.role= ${userInfo.role}`)
            if(userInfo.role == 1){
                console.log("")
                this.role = 1
            }else if(userInfo.role == 2){
                console.log("")
                this.role = 2
            }else if(userInfo.role == 3){
                console.log("")
                this.role = 3
            }else{
                this.$alert(",").then(() => {
                    this.$router.push({
                        path: '/user/login'
                    })
                })
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .navMenu
        margin-bottom 30px
</style>