<template>
    <div class="GetResume">
        <div class="get-resume-title">
            
        </div>
        <!---->
        <div class="resume-list">
            <div class="resume-left" :class="!this.isShow ? 'resume-left-flag' : ''">
                <el-table :data="tableData" style="width: 100%">
                    <el-table-column label="" style="width: 20%">
                        <template slot-scope="scope">
                            <el-popover trigger="hover" placement="top">
                                <p>: {{ scope.row.name }}</p>
                                <p>: {{ scope.row.salary }}</p>
                                <div slot="reference" class="name-wrapper">
                                    <el-tag size="medium">{{ scope.row.name }}</el-tag>
                                </div>
                            </el-popover>
                        </template>
                    </el-table-column>

                    <el-table-column
                            label=""
                            style="width: 20%"
                    >
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{scope.row.hope}}</span>
                        </template>
                    </el-table-column>

                    <el-table-column
                            label=""
                            style="width: 20%">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{scope.row.workYear}}</span>
                        </template>
                    </el-table-column>

                    <el-table-column
                            label=""
                            style="width: 20%">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{scope.row.mobile}}</span>
                        </template>
                    </el-table-column>

                    <el-table-column label="">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="showResume(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <div class="resume-right" v-if="this.isShow">
                <div class="resume-right-top">
                    <el-button type="danger" icon="el-icon-arrow-left" circle @click="hideResume"></el-button>
                </div>
                <div class="resume-right-content">
                    <el-form ref="form" :model="resume" label-width="80px">
                        <el-form-item label="">
                            <el-input v-model="resume.name" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.sex" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.year" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.workYear" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.address" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.mobile" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.email" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.hope" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.salary" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.work" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.education" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="resume.personal" disabled></el-input>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
        </div>


    </div>
</template>

<script>
    import { MygetResume } from '../utils/index';
    import { getToken } from '../utils/function';
    export default {
        name: "GetResume",
        data() {
            return {
                tableData: [{
                    name: '',
                    sex: '',
                    year: '1996.11.4',
                    workYear: '2',
                    address: '',
                    mobile: '17561723745',
                    email: 'sundapeng@haier.com',
                    hope: 'web',
                    salary: '8k',
                    work: '',
                    education: '',
                    personal: ''
                }, {
                    name: '',
                    sex: '',
                    year: '1991.11.4',
                    workYear: '4',
                    address: '',
                    mobile: '17561723745',
                    email: 'sundapeng@haier.com',
                    hope: 'web',
                    salary: '80k',
                    work: '',
                    education: '',
                    personal: ''
                }, {
                    name: '',
                    sex: '',
                    year: '1996.11.4',
                    workYear: '2',
                    address: '',
                    mobile: '17561723745',
                    email: '123123@haier.com',
                    hope: 'java',
                    salary: '998',
                    work: '23',
                    education: '998',
                    personal: ''
                }],
                isShow: false,
                resume: {
                    name: '',
                    sex: '',
                    year: '1996.11.4',
                    workYear: '2',
                    address: '',
                    mobile: '17561723745',
                    email: 'sundapeng@haier.com',
                    hope: 'web',
                    salary: '8k',
                    work: '',
                    education: '',
                    personal: ''
                }
            }
        },
        methods: {
            // 
            showResume(index, row) {
                if (!this.isShow) {
                    this.isShow = true;
                }
                this.resume.name = row.name;
                this.resume.sex = row.sex;
                this.resume.year = row.year;
                this.resume.workYear = row.workYear;
                this.resume.address = row.address;
                this.resume.mobile = row.mobile;
                this.resume.email = row.email;
                this.resume.hope = row.hope;
                this.resume.salary = row.salary;
                this.resume.work = row.work;
                this.resume.education = row.education;
                this.resume.personal = row.personal;


            },
            // 
            hideResume() {
                if (this.isShow) {
                    this.isShow = false;
                }
            },
            // 
            MygetResume() {
                let token = getToken();
                MygetResume({
                    token
                }).then((res) => {
                    let data = res.data
                    console.log(data)
                    this.tableData = data.dataList

                })
            }
        },
        mounted() {
            this.MygetResume()
        }
    }
</script>

<style lang="stylus" scoped>
    .GetResume
        .get-resume-title
            border-bottom 2px solid #eee
            padding 20px 0
            margin-bottom 40px
        .resume-list
            display flex
            .resume-left
                width 740px
                margin-right 20px
                border 1px solid #eee
            .resume-left-flag
                width 1440px
            .resume-right
                width 700px
                border 1px solid #eee
                .resume-right-top
                    padding 10px
                    box-sizing border-box
                    border-bottom 1px solid #eee
                .resume-right-content
                    padding 10px
                    box-sizing border-box
</style>