<template>
    <div class="ResumeEdit">
        <!---->
        <div class="page">
            <p class="page-p" v-if="!this.havaMyResume"></p>
        </div>
        <div class="edit-resume">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

                <el-form-item label="" prop="name">
                    <el-input v-model="ruleForm.name"></el-input>
                </el-form-item>

                <el-form-item label="" prop="sex">
                    <el-radio-group v-model="ruleForm.sex">
                        <el-radio label=""></el-radio>
                        <el-radio label=""></el-radio>
                    </el-radio-group>
                </el-form-item>

                <el-form-item label="" prop="year">
                    <el-input v-model="ruleForm.year"></el-input>
                </el-form-item>

                <el-form-item label="" prop="workYear">
                    <el-input v-model="ruleForm.workYear"></el-input>
                </el-form-item>

                <el-form-item label="" prop="address">
                    <el-input v-model="ruleForm.address"></el-input>
                </el-form-item>

                <el-form-item label="" prop="mobile">
                    <el-input v-model="ruleForm.mobile"></el-input>
                </el-form-item>

                <el-form-item label="" prop="email">
                    <el-input v-model="ruleForm.email"></el-input>
                </el-form-item>

                <el-form-item label="" prop="hope">
                    <el-input v-model="ruleForm.hope"></el-input>
                </el-form-item>

                <el-form-item label="" prop="salary">
                    <el-input v-model="ruleForm.salary"></el-input>
                </el-form-item>

                <el-form-item label="" prop="work">
                    <el-input v-model="ruleForm.work"></el-input>
                </el-form-item>

                <el-form-item label="" prop="education">
                    <el-input v-model="ruleForm.education"></el-input>
                </el-form-item>

                <el-form-item label="" prop="personal">
                    <el-input v-model="ruleForm.personal"></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')" v-if="!this.havaMyResume"></el-button>
                    <el-button type="primary" @click="updataResume('ruleForm')" v-if="this.havaMyResume"></el-button>
                    <el-button @click="resetForm('ruleForm')"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import { getMyResume, createResume, editResume } from '../utils/index';
    import { getToken } from '../utils/function';
    export default {
        name: "ResumeEdit",
        data() {
            return {
                havaMyResume: false,
                ruleForm: {
                    id: '',
                    name: '',
                    sex: '',
                    year: '',
                    workYear: '',
                    address: '',
                    mobile: '',
                    email: '',
                    hope: '',
                    salary: '',
                    work: '',
                    education: '',
                    personal: ''
                },
                rules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
                    ],
                    sex: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    year: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
                    ],
                    workYear: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }
                    ],
                    address: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 3, max: 30, message: ' 3  30 ', trigger: 'blur' }
                    ],
                    mobile: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 11, max: 11, message: ' 11 ', trigger: 'blur' }
                    ],
                    email: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
                    ],
                    hope: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 2, max: 100, message: ' 2  100 ', trigger: 'blur' }
                    ],
                    salary: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 15, message: ' 1  15 ', trigger: 'blur' }
                    ],
                    work: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 5, max: 100, message: ' 5  100 ', trigger: 'blur' }
                    ],
                    education: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 2, max: 100, message: ' 5  100 ', trigger: 'blur' }
                    ],
                    personal: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 2, max: 200, message: ' 5  200 ', trigger: 'blur' }
                    ]
                }
            };
        },
        methods: {
            // 
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let token = getToken();
                        let sex;
                        if(this.ruleForm.sex == ""){
                            sex = 1
                        }else{
                            sex = 0
                        }

                        createResume({
                            token: token,
                            name: this.ruleForm.name,
                            sex: sex,
                            year: this.ruleForm.year,
                            workYear: this.ruleForm.workYear,
                            address: this.ruleForm.address,
                            mobile: 17863253454,
                            email: this.ruleForm.email,
                            hope: this.ruleForm.hope,
                            salary: 8000,
                            work: this.ruleForm.work,
                            education: this.ruleForm.education,
                            personal: this.ruleForm.personal

                        }).then((res) => {
                            let data = res.data;
                            console.log(data)
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            // 
            updataResume(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let token = getToken();
                        let sex;
                        if(this.ruleForm.sex == ""){
                            sex = 1
                        }else{
                            sex = 0
                        }

                        editResume({
                            token: token,
                            id: this.ruleForm.id,
                            name: this.ruleForm.name,
                            sex,
                            year: this.ruleForm.year,
                            workYear: this.ruleForm.workYear,
                            address: this.ruleForm.address,
                            mobile: 17863253454,
                            email: this.ruleForm.email,
                            hope: this.ruleForm.hope,
                            salary: 8000,
                            work: this.ruleForm.work,
                            education: this.ruleForm.education,
                            personal: this.ruleForm.personal
                        }).then((res) => {
                            let data = res.data;
                            console.log(data)
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            // 
            getMyResume() {
                let token = getToken();

                // token
                getMyResume({
                    token: token
                }).then((res) => {
                    let data = res.data
                    console.log(data)
                    // 
                    if(data.data == null) {
                        console.log("")
                        this.havaMyResume = false;
                    }else {
                        console.log("")
                        this.havaMyResume = true;
                        this.ruleForm.id = data.data.id;
                        this.ruleForm.name = data.data.name;

                        this.ruleForm.year = data.data.year;
                        this.ruleForm.workYear = data.data.workYear;
                        this.ruleForm.address = data.data.address;
                        this.ruleForm.mobile = data.data.mobile;
                        this.ruleForm.email = data.data.email;
                        this.ruleForm.hope = data.data.hope;
                        this.ruleForm.salary = data.data.salary;
                        this.ruleForm.work = data.data.work;
                        this.ruleForm.education = data.data.education;
                        this.ruleForm.personal = data.data.personal;

                        if(data.data.sex == 1){
                            this.ruleForm.sex = ""
                        }else{
                            this.ruleForm.sex = ""
                        }

                    }
                })
            }
        },
        mounted() {
            // 
            this.getMyResume()
        }
    }
</script>

<style scoped>

</style>